import React, { useEffect, useState, useRef } from 'react';
import socketIOClient from 'socket.io-client';
import ChatBoxReciever, { ChatBoxSender } from './ChatBox';
import InputText from './InputText';
import UserLogin from './UserLogin';

export default function ChatContainer() {
  let socketio = socketIOClient('http://localhost:5000');
  const [chats, setChats] = useState([]);
  const [user, setUser] = useState(localStorage.getItem('user'));
  const avatar = localStorage.getItem('avatar');
  const messagesEndRef = useRef(null);

  useEffect(() => {
    socketio.on('chat', handleChat);

    socketio.on('allChatMessages', (allMessages) => {
      setChats(allMessages);
      scrollToBottom();
    });

    fetchInitialMessages();

    return () => {
      socketio.off('chat', handleChat);
    };
  }, [socketio]);

  function handleChat(senderChats) {
    setChats((prevChats) => [...prevChats, senderChats]);
    if (messagesEndRef.current) {
      const { scrollHeight, clientHeight, scrollTop } = messagesEndRef.current.parentNode;
      const scrollOffset = scrollHeight - clientHeight;
      if (scrollTop === scrollOffset) {
        scrollToBottom();
      }
    }
  }

  function sendChatToSocket(chat) {
    socketio.emit('chat', JSON.stringify(chat));
  }

  function addMessage(chat) {
    const newChat = { ...chat, user, avatar };
    setChats((prevChats) => [...prevChats, newChat]);
    sendChatToSocket(newChat);
  }

  function logout() {
    localStorage.removeItem('user');
    localStorage.removeItem('avatar');
    setUser('');
  }

  function ChatsList() {
    return (
      <div>
        {chats.map((chat, index) => {
          if (chat.user === user) {
            return <ChatBoxSender key={index} message={chat.message} avatar={chat.avatar} user={chat.user} />;
          } else {
            return <ChatBoxReciever key={index} message={chat.message} avatar={chat.avatar} user={chat.user} />;
          }
        })}
      </div>
    );
  }
  async function fetchInitialMessages() {
    try {
      const response = await fetch('/api/getChatMessages');
      const data = await response.json();
      setChats(data);
    } catch (error) {
      console.error('Failed to fetch initial chat messages:', error);
    }
  }

  function scrollToBottom() {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }

  return (
    <div>
      {user ? (
        <div>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
            <h4>You are logged in as: {user}</h4>
            <p onClick={() => logout()} style={{ color: 'blue', cursor: 'pointer' }}>
              Log Out
            </p>
          </div>
          <ChatsList />
          <InputText addMessage={addMessage} />
        </div>
      ) : (
        <UserLogin setUser={setUser} />
      )}
    </div>
  );
}

